#UtilitySection{
  color: black;
  padding-top: 70px;
  height: 400px;
  background: url("../assets/swirl_pattern.png") repeat;
}

#AboutSection{
  color: black;
  height: 400px;
  background: url("../assets/connect_@2X.png") repeat;
}

#DonateSection{
  color: black;
  height: 800px;
  background: url("../assets/foggy_birds_@2X.png") repeat;
}


#SignUpSection{
  color: black;
  font-size: large;
  height: 500px;
  background: url("../assets/ignasi_pattern_s.png") repeat;
  display: flex;
  justify-content: center;
  align-items: center;
}

#navbar{
    background: url('../assets/pink_dust.png') repeat top;
}

Input{
  max-width: 250px;
}

StudentPage{
    height: 100vh;
    background: url('../assets/hip-square.png') repeat top;
    display: flex;
  justify-content: center;
  align-items: center;
}

ProfPage{
    height: 100vh;
    background: url('../assets/tree_bark.png') repeat top;
    display: flex;
  justify-content: center;
  align-items: center;
}

.mainSection{
   height: 50vh;
   display: flex;
   justify-content: space-around;
   flex-direction: column;
}

.App-logo {
    animation: spin infinite 20s linear;
    height: 80px;
}

.App-header {
    background-color: #222;
    padding: 20px;
    color: white;
    text-align: center;
}

.App-intro {
    font-size: large;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
